import React, { useEffect, useState } from 'react';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import { Button, Grid, useMediaQuery, useTheme, Typography, Pagination } from '@material-ui/core';
import { Card } from '@mui/material';
import 'react-alice-carousel/lib/alice-carousel.css';
import 'react-awesome-slider/dist/styles.css';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/styles';
import '../../../home/Style.css';
import { getSelectedCourses } from './../../../../services/LandingPageServices';

const useStyles = makeStyles((theme) => ({
    courseCard: {
        display: 'flex',
        flexDirection: 'row',
        width: '19em',
        height: '12em',
        padding: '0px 8px 0px 8px',
        [theme.breakpoints.down('md')]: {
            width: '14em',
            height: '9em'
        }
    },
    coursesCard: {
        width: '100%',
        height: '100%',
        justifyContent: 'center'
    },
    coursesCardMd: {
        width: '80%',
        height: '80%',
        justifyContent: 'center'
    },
    coursesCardSm: {
        width: '60%',
        height: '60%',
        justifyContent: 'center'
    },
    mainCard: {
        width: '100%',
        height: '100%',
        minWidth: 200
    },
    mainCardMd: {
        width: '80%',
        height: '80%',
        minWidth: 160
    },
    mainCardSm: {
        width: '60%',
        height: '60%',
        minWidth: 200
    },

    cardImg: {
        width: '100%',
        height: '100%',
        display: 'block',
        borderRadius: 8
    },
    root: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        width: '100%',
        minWidth: 200,
        boxShadow: 'none'
    },
    rootMd: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        width: '100%',
        // minWidth: 200,
        boxShadow: 'none'
    },
    rootSm: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        width: '100%',
        minWidth: 200,
        boxShadow: 'none'
    },
    media: {
        height: 120,
        objectFit: 'cover',
        textAlign: 'center'
    },
    mediaMd: {
        height: 100,
        objectFit: 'cover',
        textAlign: 'center'
    },
    mediaSm: {
        height: 80,
        objectFit: 'cover',
        textAlign: 'center'
    },
    courseKnowCard: {
        justifyContent: 'center',
        paddingTop: '75px'
    },
    courseKnowCardMd: {
        justifyContent: 'center',
        paddingTop: '55px'
    },
    courseKnowCardSm: {
        justifyContent: 'center',
        paddingTop: '35px'
    },
    knowButton: {
        backgroundColor: '#FF8F44',
        width: '110px',
        paddingTop: '8px',
        paddingBottom: '8px',
        height: '40px'
    },
    knowButtonMd: {
        backgroundColor: '#FF8F44',
        width: '100px',
        paddingTop: '7px',
        paddingBottom: '7px',
        height: '35px',
        fontSize: '12px'
    },
    knowButtonSm: {
        backgroundColor: '#FF8F44',
        width: '90px',
        paddingTop: '6px',
        paddingBottom: '6px',
        height: '30px',
        fontSize: '10px'
    },
    coursesText: {
        fontFamily: 'Josefin Sans',
        fontWeight: 'lighter',
        fontSize: 15,
        color: '#000'
    },
    coursesTextMd: {
        fontFamily: 'Josefin Sans',
        fontWeight: 'lighter',
        fontSize: 12,
        color: '#000'
    },
    coursesTextSm: {
        fontFamily: 'Josefin Sans',
        fontWeight: 'lighter',
        fontSize: 9,
        color: '#000'
    }
}));

function CardComponent() {
    const classes = useStyles();
    const theme = useTheme();
    const matchesLG = useMediaQuery(theme.breakpoints.down('lg'));
    const [coursesList, setCoursesList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage, setItemsPerPage] = useState(4);

    const matchesSmUp = useMediaQuery(theme.breakpoints.up('sm'));
    const matchesLgUp = useMediaQuery(theme.breakpoints.up('lg'));
    const matchesMdUp = useMediaQuery(theme.breakpoints.up('md'));
    const matchesSmDown = useMediaQuery(theme.breakpoints.down('sm'));
    const matchesLgDown = useMediaQuery(theme.breakpoints.down('lg'));
    const matchesMdDown = useMediaQuery(theme.breakpoints.down('md'));

    const GetCourses = async () => {
        await getSelectedCourses()
            .then((data) => {
                setCoursesList(data.data.content || []);
            })
            .catch((error) => {
                console.log(error);
            });
    };
    const handlePageChange = (event, page) => {
        setCurrentPage(page); // Update currentPage when the page changes
    };

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const activItems = coursesList?.filter((course) => course.status === 'ACTIVE');
    const currentItems = activItems?.slice(indexOfFirstItem, indexOfLastItem);
    const colors = ['#D5FFE0', '#CCF1EC', '#DFF2FF', '#FFE5EC'];

    useEffect(() => {
        GetCourses();
    }, []);

    return (
        <>
            {matchesLgUp && (
                <Grid
                    container
                    spacing={matchesLG ? 4 : 5}
                    justifyContent="center"
                    display="flex"
                    rowSpacing="20px"
                    columnSpacing="20px"
                    paddingLeft="100px"
                >
                    {currentItems?.map((course, index) => (
                        <Grid item className={classes.coursesCard} key={course.id} xs={12} sm={6}>
                            <Card className={classes.mainCard}>
                                <Link
                                    to={{
                                        pathname: `/module-details`,
                                        state: {
                                            id: course.id,
                                            title: course.title,
                                            imageUrl: course.imageUrl,
                                            description: course.description
                                        }
                                    }}
                                    style={{ textDecoration: 'none' }}
                                >
                                    <Card className={classes.root} style={{ height: '180px', backgroundColor: colors[index] }}>
                                        <CardContent>
                                            <Typography sx={{ fontFamily: 'Josefin Sans', fontWeight: 'lighter', fontSize: '20px' }}>
                                                {course.title.replace(/_/g, ' ')}
                                            </Typography>
                                            <Typography
                                                sx={{ fontSize: 15, color: '#ff8f44', fontFamily: 'Josefin Sans', fontWeight: 'lighter' }}
                                            >
                                                {course.minAge}-{course.maxAge} years
                                            </Typography>
                                        </CardContent>
                                        <CardMedia className={classes.media} title={course.title}>
                                            <img src={course.imageUrl} style={{ height: '120px' }} alt="Course" />
                                        </CardMedia>
                                    </Card>
                                    <CardContent sx={{ justifyContent: 'start', paddingTop: '10px', height: '50px' }}>
                                        <Typography className={classes.coursesText}>
                                            {course.description.length > 40
                                                ? `${course.description.substring(0, 180)}...`
                                                : course.description}
                                        </Typography>
                                    </CardContent>
                                    <CardActions className={classes.courseKnowCard}>
                                        <Button variant="contained" className={classes.knowButton}>
                                            Know More
                                        </Button>
                                    </CardActions>
                                </Link>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            )}

            {matchesLgDown && matchesMdUp && (
                <Grid
                    container
                    spacing={3}
                    justifyContent="center"
                    display="flex"
                    rowSpacing="10px"
                    columnSpacing="10px"
                    paddingLeft="10px"
                >
                    {currentItems?.map((course, index) => (
                        <Grid item className={classes.coursesCardMd} key={course.id} xs={9} sm={6}>
                            <Card className={classes.mainCardMd}>
                                <Link
                                    to={{
                                        pathname: `/module-details`,
                                        state: {
                                            id: course.id,
                                            title: course.title,
                                            imageUrl: course.imageUrl,
                                            description: course.description
                                        }
                                    }}
                                    style={{ textDecoration: 'none' }}
                                >
                                    <Card className={classes.rootMd} style={{ height: '140px', backgroundColor: colors[index] }}>
                                        <CardContent>
                                            <Typography sx={{ fontFamily: 'Josefin Sans', fontWeight: 'lighter', fontSize: '16px' }}>
                                                {course.title.replace(/_/g, ' ')}
                                            </Typography>
                                            <Typography
                                                sx={{ fontSize: 12, color: '#ff8f44', fontFamily: 'Josefin Sans', fontWeight: 'lighter' }}
                                            >
                                                {course.minAge}-{course.maxAge} years
                                            </Typography>
                                        </CardContent>
                                        <CardMedia className={classes.mediaMd} title={course.title}>
                                            <img src={course.imageUrl} style={{ height: '100px' }} alt="Course" />
                                        </CardMedia>
                                    </Card>
                                    <CardContent sx={{ justifyContent: 'start', paddingTop: '4px', height: '80px' }}>
                                        <Typography className={classes.coursesTextMd}>
                                            {course.description.length > 40
                                                ? `${course.description.substring(0, 180)}...`
                                                : course.description}
                                        </Typography>
                                    </CardContent>
                                    <CardActions className={classes.courseKnowCardMd}>
                                        <Button variant="contained" className={classes.knowButtonMd}>
                                            Know More
                                        </Button>
                                    </CardActions>
                                </Link>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            )}

            {matchesMdDown && matchesSmUp && (
                <Grid
                    container
                    spacing={3}
                    justifyContent="center"
                    display="flex"
                    rowSpacing="10px"
                    columnSpacing="10px"
                    paddingLeft="10px"
                >
                    {currentItems?.map((course, index) => (
                        <Grid item className={classes.coursesCardMd} key={course.id} xs={6} sm={5}>
                            <Card className={classes.mainCardMd}>
                                <Link
                                    to={{
                                        pathname: `/module-details`,
                                        state: {
                                            id: course.id,
                                            title: course.title,
                                            imageUrl: course.imageUrl,
                                            description: course.description
                                        }
                                    }}
                                    style={{ textDecoration: 'none' }}
                                >
                                    <Card className={classes.rootMd} style={{ height: '140px', backgroundColor: colors[index] }}>
                                        <CardContent>
                                            <Typography sx={{ fontFamily: 'Josefin Sans', fontWeight: 'lighter', fontSize: '16px' }}>
                                                {course.title.replace(/_/g, ' ')}
                                            </Typography>
                                            <Typography
                                                sx={{ fontSize: 12, color: '#ff8f44', fontFamily: 'Josefin Sans', fontWeight: 'lighter' }}
                                            >
                                                {course.minAge}-{course.maxAge} years
                                            </Typography>
                                        </CardContent>
                                        <CardMedia className={classes.mediaMd} title={course.title}>
                                            <img src={course.imageUrl} style={{ height: '100px' }} alt="Course" />
                                        </CardMedia>
                                    </Card>
                                    <CardContent sx={{ justifyContent: 'start', paddingTop: '4px', height: '80px' }}>
                                        <Typography className={classes.coursesTextMd}>
                                            {course.description.length > 40
                                                ? `${course.description.substring(0, 180)}...`
                                                : course.description}
                                        </Typography>
                                    </CardContent>
                                    <CardActions className={classes.courseKnowCardMd}>
                                        <Button variant="contained" className={classes.knowButtonMd}>
                                            Know More
                                        </Button>
                                    </CardActions>
                                </Link>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            )}

            {matchesSmDown && (
                <Grid
                    container
                    spacing={3}
                    justifyContent="center"
                    display="flex"
                    rowSpacing="20px"
                    columnSpacing="20px"
                    paddingLeft="100px"
                >
                    {currentItems?.map((course, index) => (
                        <Grid item className={classes.coursesCardSm} key={course.id} xs={9} sm={6}>
                            <Card className={classes.mainCardSm}>
                                <Link
                                    to={{
                                        pathname: `/module-details`,
                                        state: {
                                            id: course.id,
                                            title: course.title,
                                            imageUrl: course.imageUrl,
                                            description: course.description
                                        }
                                    }}
                                    style={{ textDecoration: 'none' }}
                                >
                                    <Card className={classes.rootSm} style={{ height: '120px', backgroundColor: colors[index] }}>
                                        <CardContent>
                                            <Typography sx={{ fontFamily: 'Josefin Sans', fontWeight: 'lighter', fontSize: '12px' }}>
                                                {course.title.replace(/_/g, ' ')}
                                            </Typography>
                                            <Typography
                                                sx={{ fontSize: 10, color: '#ff8f44', fontFamily: 'Josefin Sans', fontWeight: 'lighter' }}
                                            >
                                                {course.minAge}-{course.maxAge} years
                                            </Typography>
                                        </CardContent>
                                        <CardMedia className={classes.mediaSm} title={course.title}>
                                            <img src={course.imageUrl} style={{ height: '100px' }} alt="Course" />
                                        </CardMedia>
                                    </Card>
                                    <CardContent sx={{ justifyContent: 'start', paddingTop: '5px', height: '50px' }}>
                                        <Typography className={classes.coursesTextSm}>
                                            {course.description.length > 40
                                                ? `${course.description.substring(0, 180)}...`
                                                : course.description}
                                        </Typography>
                                    </CardContent>
                                    <CardActions className={classes.courseKnowCardSm}>
                                        <Button variant="contained" className={classes.knowButtonSm}>
                                            Know More
                                        </Button>
                                    </CardActions>
                                </Link>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            )}
            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', marginTop: '20px' }} className="pagination">
                {coursesList.length > itemsPerPage && (
                    <Pagination
                        page={currentPage}
                        count={Math.ceil(coursesList.length / itemsPerPage)} // Calculate the total number of pages
                        onChange={handlePageChange}
                    />
                )}
            </div>
        </>
    );
}

export default CardComponent;
